import { PortalBaseViewController } from '@/views/portal/PortalBaseViewController';
import { PortalResetForgotPasswordController } from '@/views/portal/sign_in/PortalResetForgotPasswordController';
import { TTAPI } from '@/services/TimeTrexClientAPI';
import { IndexViewController } from '@/portal/recruitment/IndexController';
import { ServiceCaller } from '@/services/ServiceCaller';
import { PortalForgotPasswordController } from '@/views/portal/sign_in/PortalForgotPasswordController';

export class SignInController extends PortalBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			doing_login: false,
			edit_view_error_ui_dic: {},

			_delegate: null,
			events: {
				'click #forgot_password': 'forgotPasswordClick'
			}
		} );

		super( options );
	}

	init( options ) {
		var $this = this;
		$this.recruitment_api = TTAPI.APIRecruitmentAuthentication;
		$this.jobapplicant_api = TTAPI.APIJobApplicantPortal;
		$this.authentication_api = TTAPI.APIAuthentication;
		// var login_data = LocalCacheData.getLoginData();
		var el = Global.loadWidget( 'views/portal/sign_in/SignIn.html' );
		$this.setElement( _.template( el )() );
		$this.user_name = $this.$( '#user_name' );
		$this.password = $this.$( '#password' );
		if ( Global.isSet( options ) && Global.isSet( options._delegate ) ) {
			$this._delegate = options._delegate;
		}
		if ( LocalCacheData.getAllURLArgs().sm === 'ResetPassword' && LocalCacheData.getAllURLArgs().key && LocalCacheData.getAllURLArgs().company_id ) {
			var forgotResetPasswordIntance = new PortalResetForgotPasswordController( {
				callback: function() {
					LocalCacheData.setAllURLArgs( _.omit( LocalCacheData.getAllURLArgs(), 'sm' ) );
					LocalCacheData.setAllURLArgs( _.omit( LocalCacheData.getAllURLArgs(), 'key' ) );
					IndexViewController.instance.router.navigate( '#!m=PortalJobVacancy&company_id=' + LocalCacheData.getAllURLArgs().company_id, {
						trigger: false,
						replace: true
					} );
					$this.setLoginTips( $.i18n._( 'Password has been changed successfully, you may now login' ) );
				}
			} );
		} else {
			// $( this.el ).visible();
			$this.render();
		}
		// this.render();
	}

	render() {
		var $this = this;
		LocalCacheData.setSessionID( '' );
		this.user_name.focus();
		this.password.unbind( 'keydown' ).bind( 'keydown', function( e ) {
			if ( e.keyCode === 13 ) {
				$this.onLoginBtnClick();
			}
		} );
		if ( LocalCacheData.getAllURLArgs().user_name ) {
			this.user_name.val( LocalCacheData.getAllURLArgs().user_name );
		}
		if ( LocalCacheData.getAllURLArgs().password ) {
			this.password.val( LocalCacheData.getAllURLArgs().password );
		}
		this.showSignInModal();
	}

	showSignInModal() {
		var $this = this;
		IndexViewController.instance.router.showSignInModal( this.$el, {
			title: $.i18n._( 'Login' ),
			actions: [
				{ label: $.i18n._( 'Cancel' ), isClose: true },
				{
					label: $.i18n._( 'Login' ), callBack: function( e ) {
						$this.onLoginBtnClick();
					}
				}
			]
		} );
	}

	hideSignInModal( callback ) {
		if ( callback ) {
			$( '#signinModal' ).unbind( 'hidden.bs.modal' ).bind( 'hidden.bs.modal', function( e ) {
				callback();
			} );
		}
		IndexViewController.instance.router.hideSignInModal();
	}

	forgotPasswordClick() {
		var $this = this;
		this.hideSignInModal( function() {
			var forgotPasswordIntance = new PortalForgotPasswordController();
			$( '#signinModal' ).unbind( 'hidden.bs.modal' );
		} );
	}

	onLoginBtnClick() {
		var user_name = this.user_name.val();
		var password = this.password.val();
		var $this = this;
		if ( !this.doing_login ) {
			this.doing_login = true;
		} else {
			return;
		}
		//Async call
		this.recruitment_api.Login( user_name, password, {
			onResult: function( e ) {
				$this.onLoginSuccess( e );
				$this.setLocalCacheCompany();
			}, delegate: this
		} );
	}

	setLocalCacheCompany() {
		// set local cache company information this will be used when sending an error report.
		this.authentication_api.getCurrentCompany( {
			onResult: function( current_company_result ) {
				var com_result = current_company_result.getResult();
				LocalCacheData.setCurrentCompany( com_result );
			}
		} );
	}

	onLoginSuccess( e, session_id ) {
		var result;
		var $this = this;
		if ( !session_id ) {
			result = e.getResult();
		} else {
			result = session_id;
		}
		//Clean user preference, since if login to normal UI and than to portal, it's not null.
		LocalCacheData.setLoginUserPreference( null );

		if ( e && !e.isValid() ) {
			LocalCacheData.setSessionID( '' );
			deleteCookie( Global.getSessionIDKey() );

			if ( e.getDetails()[0].hasOwnProperty( 'password' ) ) {
				$this.hideSignInModal();
				$this.password.val( '' );
				IndexViewController.openWizard( 'ResetPasswordWizard', {
					user_name: $this.user_name.val(),
					api_class: TTAPI.APIRecruitmentAuthentication,
					message: e.getDetailsAsString()
				}, function() {
					$this.setLoginTips( $.i18n._( 'Password has been changed successfully, you may now login' ) );
				} );
			} else {
				// TAlertManager.showAlert( e.getDetailsAsString(), 'Error', function() {
				//     password.focus();
				// } );
				this.setErrorTips( e );
			}

			$this.doing_login = false;
		} else {
			this.clearErrorTips();
			ServiceCaller.cancel_all_error = false;

			IndexViewController.setPortalLoginUserData( result, function() {
				IndexViewController.instance.router.headerView.render();
				if ( $this._delegate ) {
					$this._delegate.render();
				}
				$this.hideSignInModal();

				$this.doing_login = false;
			} );
		}
	}

	setLoginTips( message ) {
		if ( typeof this.$( '.alert' )[0] !== 'undefined' ) {
			this.$( '.alert' ).remove();
		}
		var row = Global.loadWidget( 'views/portal/alert/alert.html' );
		this.$( '.signIn-form' ).before( row );
		this.$( '.alert' ).append( '<p>' + message + '</p>' );
		this.showSignInModal();
	}

	setErrorTips( e ) {
		var error_list = e.getDetails() ? e.getDetails()[0] : {};
		if ( error_list && error_list.hasOwnProperty( 'error' ) ) {
			error_list = error_list.error;
		}
		for ( var key in error_list ) {
			if ( !error_list.hasOwnProperty( key ) ) {
				continue;
			}
			var field_obj;
			if ( this.$( 'input[id="' + key + '"]' )[0] ) {
				field_obj = this.$( 'input[id="' + key + '"]' );
			}
			if ( field_obj ) {
				field_obj.addClass( 'error-tip' );
				var error_string;
				if ( _.isArray( error_list[key] ) ) {
					error_string = error_list[key][0];
				} else {
					error_string = error_list[key];
				}
				field_obj.tooltip( {
					'title': error_string
				} );
				field_obj.tooltip( 'show' );
				this.edit_view_error_ui_dic[key] = field_obj;
				// sort_array.push( field_obj.attr('data-sort') );
			}
		}
	}

	clearErrorTips() {
		for ( var key in this.edit_view_error_ui_dic ) {
			this.edit_view_error_ui_dic[key].removeClass( 'error-tip' );
			this.edit_view_error_ui_dic[key].tooltip( 'dispose' );
		}
		this.edit_view_error_ui_dic = {};
	}
}