import { TTBackboneView } from '@/views/TTBackboneView';
import { SignInController } from '@/views/portal/sign_in/SignInController';
import { HeaderUploadResumeWidget } from '@/views/portal/header/HeaderUploadResumeWidget';
import { ServiceCaller } from '@/services/ServiceCaller';
import { TTAPI } from '@/services/TimeTrexClientAPI';

export class HeaderViewController extends TTBackboneView {
	constructor( options = {} ) {
		_.defaults( options, {
			events: {
				'click .navbar-signin .login': 'goPortalLogin',
				'click .navbar-signin .logout': 'doLogout',
				'click .navbar-signin .profile': 'goProfile'
				// 'click .navbar-upload-resume > a': 'uploadResume'
			}
		} );

		super( options );
	}

	initialize( options ) {
		super.initialize( options );
		var url;
		var options = {};
		if ( LocalCacheData.getAllURLArgs().company_id ) {
			url = ServiceCaller.getAPIURL( 'Class=APIDocumentPortal&Method=getCompanyLogo' + '&v=2' ) + '&company_id=' + LocalCacheData.getAllURLArgs().company_id;
		} else {
			url = '../../theme/default/portal/images/t_icon_logo.png';
		}
		var tpl = Global.loadWidget( 'views/portal/header/HeaderView.html' );
		this.setElement( _.template( tpl )( { company_logo: url } ) );
		this.uploadResumeWidget = null;
		this.profileView = null;
		this.jobVacancyDetailController = null;
		this.jobVacancyViewController = null;
		this.render();
	}

	render() {
		var $this = this;
		if ( !LocalCacheData.getPortalLoginUser() ) {
			var sign_in_container = $( '<a class="login"></a>' );
			sign_in_container.attr( 'href', 'javascript:;' );
			sign_in_container.append( $( '<img>' ).attr( 'src', '../../theme/default/portal/images/t_icon_head.png' ) );
			sign_in_container.append( $( '<span>' ).text( $.i18n._( 'Login' ) ) );
			this.$( '.navbar-signin' ).html( sign_in_container );
		} else {
			var my_profile_container = $( '<div class="dropdown">' );
			my_profile_container.append( '<a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">' + LocalCacheData.getPortalLoginUser().first_name + ' ' + LocalCacheData.getPortalLoginUser().last_name + ' <span class="caret"></span></a>' );
			var dropdown_menus = $( '<ul class="dropdown-menu">' );
			dropdown_menus.append( '<li><a class="profile" href="javascript:;">My Profile</a></li>' );
			dropdown_menus.append( '<li><a class="logout" href="javascript:;">Logout</a></li>' );
			my_profile_container.append( dropdown_menus );
			this.$( '.navbar-signin' ).html( my_profile_container );
		}
	}

	goPortalLogin() {
		var sign_controller = new SignInController( {
			_delegate: this.jobVacancyDetailController
		} );
		// $('#signinModal').modal({
		// 	backdrop: 'static',
		// 	show: true
		// });
	}

	doLogout() {
		this.clearErrorTips();
		var $this = this;
		var current_user_api = TTAPI.APIRecruitmentAuthentication;
		current_user_api.Logout( {
			onResult: function( result ) {
				deleteCookie( Global.getSessionIDKey() );
				LocalCacheData.setPortalLoginUser( false );
				LocalCacheData.current_open_view_id = ''; //#1528  -  Logout icon not working.
				// $this.goPortalLogin();
				// $this.render();
				// if ( $this.jobVacancyDetailController ) {
				// 	$this.jobVacancyDetailController.render();
				// }

				if ( $this.jobVacancyViewController ) {
					$this.render();
				} else {
					IndexViewController.instance.router.navigate( '#!m=PortalJobVacancy&company_id=' + LocalCacheData.getAllURLArgs().company_id, {
						trigger: true,
						replace: true
					} );
				}
			}
		} );
	}

	//needed for logout.
	clearErrorTips() {
		//harder here because we can't access the active controller's data from here.
		var tips = $( '.error-tip' );
		for ( var i = 0; i < tips.length; i++ ) {
			$( tips[i] ).removeClass( 'error-tip' );
			$( tips[i] ).tooltip().tooltip( 'dispose' );
		}
	}

	goProfile() {
		var location = Global.getBaseURL() + '#!m=MyProfile&company_id=' + LocalCacheData.getAllURLArgs()['company_id'];
		window.location = location;
	}

	uploadResume() {
		var $this = this;
		if ( !LocalCacheData.getPortalLoginUser() ) {
			// IndexViewController.instance.router.showTipModal('Please login first!')
			// setTimeout(function(  ) {
			// 	$this.goPortalLogin();
			// }, 100)
			this.goPortalLogin();
		} else {
			if ( !this.uploadResumeWidget ) {
				this.uploadResumeWidget = new HeaderUploadResumeWidget( {
					profileView: $this.profileView
				} );
			}
			this.uploadResumeWidget.render();
		}
	}
}
